const  setData = (dataLength = 30) => {
  return new Array(dataLength).fill(0).map((ele, idx) => {
    return {
      id : idx+1, 
      title : `title${idx+1}`,
      writer : `name${idx+1}`,
      content : `content${idx+1}`,
      date : `2022-1-${idx+1}`
    }
  })
}
const totData = setData(200)
module.exports = {
  totData,

  initialState : {
    data : totData.slice().reverse().slice(0,5),
    page : 1,
    size : 5, 
    order : 'dsc',
    filter : '',
    name : '',
    total : totData.length,
  }
  
}

